import React, { Fragment, useState } from "react";
import Detail from "../components/Detail";
import Grid from "../components/Grid";
import Title from "../components/Title";
import "./layout.scss";

const Projects = () => {

    const [detail, setdetail] = useState({show: false, proyect: {}});

    const handleDetail = (proyect) => {
        setdetail({show:true, proyect: proyect})
    }

    const toggleDetail = () =>{
        setdetail({show:false, proyect: {}})
    }

    return (
        <section className="global-container">
            <Title title="Proyectos." />
            <Grid detail={handleDetail} />

            {
                detail.show ? (
                    <Detail close={toggleDetail} proyect={detail.proyect} />
                ) : null
            }

        </section>
    )
};

export default Projects;