import React from "react";
import "./grid.scss";
import projects from "./../data/proyects.json";
import Card from "./Card";

const Grid = ({detail}) => {
    return (
        <section className="grid">
            {
                projects.map((p, index) =>
                    <Card proyect={p} index={index} detail={detail} />
                )
            }
        </section>
    )
};

export default Grid;