import React from "react";
import "./card.scss";
import location from "./../assets/location.png"   

const Card = ({ proyect, index, detail }) => {

    const openDetai = () => {
        detail(proyect)
    }

    return(
        <div className="card" onClick={openDetai } >
            <div className="img-container">
                {
                    proyect.image !== "NA" ? (
                        <img src={proyect.image} />
                    ) : (
                        null
                    )
                }
                <h2>{proyect.name}</h2>
            </div>
            <div className="card-info">
                <div className="info-row">
                    <p> Tipo: <span>{proyect.type}</span> </p>
                </div>
                <div className="info-row">
                    <img src={location} />
                    <p>{proyect.location}</p>
                </div>
            </div>
        </div>
    )
};

export default Card;